<template>
    <div class="sidebar">
        <el-row class="tac">
            <el-col :span="20" class="col">
                <el-menu :default-active="myindex" class="el-menu-vertical-demo" background-color="#545c64"
                    text-color="#fff" active-text-color="#ffd04b">
                    <!-- <el-menu-item index="1" @click="goDevelop">
                        <span slot="title">开发文档</span>
                    </el-menu-item> -->
                    <el-menu-item index="2" @click="goUpdate">
                        <span slot="title">更新日志</span>
                    </el-menu-item>
                    <el-menu-item index="3" @click="goAbout">
                        <span slot="title">关于我们</span>
                    </el-menu-item>
                </el-menu>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    name: 'Sidebar2',
    methods: {
        goDevelop() {
            this.$router.push({ path: '/about/develop' })
        },
        goUpdate() {
            this.$router.push({ path: '/about/update' })
        },
        goAbout() {
            this.$router.push({ path: '/about/aboutUs' })
        },
        demo(data) {
            this.myindex = data
            console.log(data);
        }
    },
    data() {
        return {
            myindex: '2'
        }
    },
    mounted() {
        if(this.$route.path=='/about/update'){
            this.myindex = '2'
        }else if(this.$route.path=='/about/aboutUs'){
            this.myindex = '3'
        }
    },
}
</script>

<style lang="scss" scoped>
.sidebar {
    float: left;

    .tac {
        width: 200px;
        height: 650px;
        background-color: rgba(84, 92, 100);

        .col {
            width: 100%;
            transform: translateY(20px);
        }
    }
}
